h3 {
  color: #bfa181;
  text-align: center;
  padding: 10px;
}

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-panel {
  display: flex;
  background-color: #15355a;
  margin: 20px;
  border-radius: 13px;
  width: 60%;
}

.background-image {
  width: 60%;
  height: 100%; /* Full height for the image */
  border-radius: 13px 0 0 13px; /* Rounded corners on the left side */
}

.text-panel {
  flex: 1;
  padding: 20px;
  text-align: left;
  color: black;
  text-align: center;
  border-radius: 0 13px 13px 0; /* Rounded corners on the right side */
}

.first-panel {
  display: flex;
  width: 100%;
}

/* Media query for screens smaller than 600px */
@media only screen and (max-width: 900px) {
  .image-panel {
    flex-direction: column; /* Display image and text in a column on mobile */
    width: 80%; /* Full width for the image in mobile view */
    align-items: center; /* Center the image horizontally in mobile view */
  }

  .text-panel {
    width: 100%; /* Full width for the text in mobile view */
    border-radius: 0 0 13px 13px; /* Rounded corners on the bottom for the text in mobile view */
  }
}
