p {
  color: white;
}
.order {
  background-color: rgb(39, 39, 126);
  width: 50%;
  padding: 10px;
  border-radius: 20px;
  margin: 20px auto;
}
.orderDetail table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.orderDetail th,
.orderDetail td {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

.orderDetail th {
  background-color: rgb(190, 95, 31);
}

.orderDetail tr:hover {
  background-color: #8f94dd;
}
