header {
  margin: 0 auto;
  font-size: 1.1em;
  font-weight: 600;
}
header ul {
  list-style-type: none;
  text-align: center;
  margin: 10px;
}
header ul li {
  margin-right: 60px;
  display: inline;
}
header a {
  text-decoration: none;
  color: #178582;
  font-size: 20px;
}
header a:hover {
  color: #bfa181;
}
hr {
  height: 1px;
  background-color: #bfa181;
  border: none;
}
#alertBox {
  background-color: rgb(32, 48, 138);
  text-align: center;
  color: white;
  padding: 5px;
}
header img {
  width: 70px;
  display: inline-block;
  margin: 0 auto;
  text-align: center;
  padding: 10px 0;
  vertical-align: middle;
}
.checkout {
  position: absolute;
  right: 20px;
  top: 65px;
}
.checkout a {
  font-size: 55px;
}
.checkout a span {
  padding-left: 25px;
  padding-top: 17px;
  color: rgb(207, 150, 42);
  position: absolute;
}
.footer-container {
  background-color: #314c66;
  padding: 20px;
  text-align: center;
  display: flex;
  bottom: 0;
}

.footer-container div {
  max-width: 800px;
  margin: 0 auto;
}
.mobile-menu-button {
  display: none;
  font-size: 20px;
  background-color: transparent;
  border: none;
  color: #178582;
  cursor: pointer;
}

.mobile-menu {
  display: none;
}

@media only screen and (max-width: 900px) {
  .mobile-menu-button {
    display: block;
  }

  header ul {
    display: none;
    flex-direction: column;
  }
  header img {
    display: none;
  }
  header ul li {
    margin: 0;
    padding: 5px;
    text-align: left;
  }

  header ul.show {
    display: flex;
  }
}
