img {
  width: 100%;
  left: 0;
}
* {
  scrollbar-width: auto;
  scrollbar-color: #bfa181 #0a1828;
}
.confTab button {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 15px;
  border-radius: 13px;
}

*::-webkit-scrollbar {
  width: 1px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #bfa181;
  border-radius: 10px;
}
.pcImg h1 {
  color: #bfa181;
}
.desc p {
  color: #696561;
}
.desc b {
  color: rgb(179, 176, 176);
  display: block;
  padding: 20px 0px;
}

.component:hover {
  background-color: #e0e0e0;
  cursor: pointer;
}

.confTab {
  grid-column: 2;
  grid-row: 1;
  max-height: 600px;
  overflow-y: auto;
  overflow-x: hidden;
  vertical-align: middle;
}
.custom-container {
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 20px 0 200px;
  width: 70%;
  margin: 0 auto;
}

.alertBox {
  width: 60%;
  margin: 0 auto;
}
.selectTab {
  width: 80%;
  background-color: rgb(255, 255, 255);
  padding: 5px 10px;
  font-size: 10px;
  text-align: left;
  margin: 10px auto;
  border-radius: 10px;
  border: 3px #e2b687 solid;
  cursor: pointer;
}

.selectTab:hover {
  box-shadow: 0px 0px 20px #c7914a;
}
.confTab img {
  width: 50px;
}

.component {
  border: 1px black solid;
  display: grid;
  padding: 10px;
  margin: 5px;
  grid-template-columns: 0.5fr 2fr 1fr;
  border-radius: 13px;
  min-height: 30px;
  vertical-align: middle;
}

.cpu-scrollview {
  max-height: 250px;
  overflow-y: auto;
}
.listCPU {
  display: none;
}

.visible {
  display: block;
}

.hidden {
  display: none;
}
span {
  font-size: 16px;
}

.pcImg {
  display: grid;
  background-color: #254569;
  border-radius: 20px;
  padding: 10px;
  margin-top: 10px;
  box-shadow: 0px 0px 20px #bfa181;
  grid-template-columns: 1fr 0.5fr;
  grid-template-rows: 1fr 0fr;
}
.left {
  grid-column: 1;
}
.right {
  padding: 0 20px;
  text-align: center;
  margin: auto 0;
  grid-column: 2;
  grid-row: 1;
}
.choosen {
  color: white;
  background-color: black;
  border: none;
  padding: 20px 30px;
  border-radius: 5px;
  cursor: pointer;
  font-size: large;
}
.pcImg img {
  width: 100%;
  min-width: 100px;
}
.unChoosen {
  color: rgb(122, 121, 121);
  background-color: rgb(231, 222, 222);
  border: none;
  padding: 20px 30px;
  border-radius: 5px;
  pointer-events: none;
  font-size: large;
}
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 160px;
  background-color: #254569;
  color: #bfa181;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 50%;
  margin-top: 5px;
  margin-left: -80px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.yourCustom button {
  background-color: black;
  border: none;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  margin-left: 10px;
  cursor: pointer;
}

@media only screen and (max-width: 1150px) {
  .pcImg {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.5fr 0.3fr;
  }
  .left {
    grid-column: 1;
    grid-row: 1;
  }
  .left img {
    max-width: 600px;
    padding-left: 10%;
  }
  .right {
    grid-column: 1;
    grid-row: 2;
  }
  .custom-container {
    grid-template-columns: 1fr;
  }

  .confTab {
    grid-column: 1;
    grid-row: 2;
  }
}
