.products-container {
  margin: 0 auto;
  text-align: center;
  width: 80%;
  padding: 10px;
}
.product-card h2,
b {
  color: gold;
}
.product-card {
  border: 1px solid white;
  display: inline-block;
  width: 20%;
  text-align: left;
  margin: 13px;
  padding: 13px;
  border-radius: 13px;
  min-width: 320px;
}
.product-card button {
  background-color: black;
  color: white;
  border: none;
  font-size: 13px;
  padding: 10px 15px;
  border-radius: 13px;
  cursor: pointer;
  margin-left: 45%;
}
.product-card button:hover {
  background-color: rgb(32, 32, 32);
}
