.cart-container {
  width: 70%;
  color: white;
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 0.1fr 0.6fr;
  text-align: center;
}
.cart-container td button {
  display: inline;
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
}
td {
  width: 60px;
}
table {
  grid-column: 1;
}
.checkoutPanel {
  margin-top: 80px;
  background-color: rgb(32, 58, 80);
  border-radius: 20px;
  grid-column: 3;
  height: 300px;
  grid-column: 3;
  grid-row: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.1fr 0.1fr 0.3fr 0.4fr;
  padding: 5%;
  box-shadow: 0px 0px 10px rgb(70, 68, 204);
}

.components p {
  font-weight: bold;
}
.checkoutPanel p {
  text-align: left;
  padding: 10px;
}
.checkoutPanel h2 {
  text-align: right;
}
.checkoutPanel button {
  grid-column: span 2;
  grid-row: 4;
  background-color: black;
  color: white;
  border: none;
  font-size: 20px;
  padding: 5px 15px;
  border-radius: 13px;
  cursor: pointer;
}
.checkoutPanel button:hover {
  background-color: rgb(32, 32, 32);
}
.adressDetails {
  grid-column: 1;
  min-height: 400px;
  padding-bottom: 40px;
  text-align: start;
  border-radius: 13px;
  padding: 0px 20px 50px;
}

label {
  position: absolute;
  margin-left: 10px;
  background: #0a1828;
  margin-left: 20px;
  padding: 0 5px;
  border-radius: 5px;
}

input {
  width: calc(100% - 20px); /* Adjust the width based on your layout */
  padding: 16px;
  box-sizing: border-box;
  border: 1px solid #cccccc7a;
  margin: 5px;
  border-radius: 4px;
  color: white;
  background-color: transparent;
}
.emptyCart h1 {
  color: white;
  text-align: center;
  height: 630px;
}
@media only screen and (max-width: 900px) {
  .cart-container {
    grid-template-columns: 1fr;
  }
  .checkoutPanel {
    grid-column: 1;
  }
}
